:root {
  --yellow: #fed700;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Rubik;
}

html {
  font-size: 10px;
}
a {
  text-decoration: none;
}

ul li {
  list-style: none;
}

/* Navbar design start */

.nav_container {
  max-width: 1328px;
  margin: 0 auto;
  height: 80px;
  display: flex;
  align-items: center;
  /* margin-left: 70px; */
  padding: 0 3%;
}
.before_nav_container {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.nav_menu {
  height: 100%;
  display: contents;
}
nav {
  height: 100%;
}
.nav_logo {
  width: 125px;
  height: 60px;
  /* border-right: 1px solid rgba(255, 255, 255, 0.1); */
  display: flex;
  align-items: center;
  padding-right: 25px;
}
.nav_logo img {
  width: 100%;
  height: 21px;
}
.nav_ul {
  display: flex;
  padding: 0 0 0 25px;
  gap: 15px;
  align-items: center;
  height: 100%;
}

.nav_ul li {
  font-family: Rubik;
  font-size: 18px;
  color: white;
  cursor: pointer;
  height: 100%;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
}
.nav_ul li:hover {
  color: rgb(178, 178, 178);
}

.header_style {
  /* background-position: center; */
  background-size: cover;
  background-repeat: no-repeat;
  /* height: 100vh; */
}
.hero_content {
  max-width: 1328px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 3%;
  /* margin-left: 108px; */
}
.hero_content_left {
  margin-top: 50px;
  max-width: 600px;
}
.hero_content_left img {
  width: 100%;
}
.hero_content_left h2 {
  color: white;
  font-family: "Open Sans";
  font-size: 42px;
  line-height: 100px;
  font-weight: 300;
}
.hero_content_left h3 {
  color: white;
  font-family: "Open Sans";
  font-size: 38px;
  line-height: 38px;
  font-weight: 700;
}
.hero_content_left_image {
  display: flex;
  gap: 50px;
  margin-top: 50px;
  margin-left: 30px;
}
.hero_content_left_image_first img {
  width: 143px;
}
.hero_content_left_image_first p {
  color: white;
}

.hero_content_left_image_second img {
  width: 71px;
}
.hero_content_left_image_second p {
  color: white;
}
.hero_content_right_in_responsive {
  display: none;
}
.hero_content_right {
  max-width: 500px;
}
.hero_content_right img {
  width: 100%;
}

.language_block {
  width: 180px;
  height: 40px;
  position: fixed;
  bottom: auto;
  top: 0;
  right: 10%;
  border-radius: 0 0 5px 5px;
  background: #1e1e1e;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  align-items: center;

  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: all 0.4s ease-in-out;
}
.language_block:hover {
  height: 70px;
}
.lang_change {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  padding: 0px 0px 0px 0px;
  padding-top: 10px;
  gap: 15px;
  cursor: pointer;
}

.lang_change img {
  width: 28px;
  height: 16px;
}
.lang_change p {
  color: #bdbdbd;
}
/* NAVBAR END */

/* burger-menu */
.burger_menu {
  display: none;
  position: absolute;
  top: 30px;
  right: 20px;
  font-size: 1.2rem;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}
.burger_class {
  display: block;
  width: 25px;
  height: 3px;
  border-radius: 8px;
  background: #d2232d;
}
.burger_class:nth-child(2) {
  margin: 5px 0;
}

/* navbarMobile */

.nav_mobile {
  display: none;
  position: fixed;
  width: 280px;
  height: auto;
  top: 80px;
  right: -100%;
  background-color: #000;
  z-index: 999;
}

.open_mobile {
  right: 0;
  transition: 0.5s;
}

.nav_mobile ul li {
  border-bottom: 1px solid #d2232d;
  width: 100%;
}

.nav_mobile ul li a {
  display: block;
  padding: 10px 20px;
  width: 100%;
  font-family: Rubik;
  font-size: 18px;
  color: white;
}

.nav_mobile .dropdown_mobile li {
  border-bottom: none;
  width: 100%;
}

.nav_mobile .dropdown_mobile li a {
  padding: 4px 40px;
}

.nav_mobile .dropdown_mobile li a::before {
  display: absolute;
  content: "- ";
}

.language_block_mobile {
  display: flex;
}

.lang_change_mobile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  padding: 10px 5px 10px 20px;
  gap: 5px;
  cursor: pointer;
}

.lang_change_mobile img {
  width: 28px;
  height: 16px;
}
.lang_change_mobile p {
  color: #bdbdbd;
}

/* Sections at home page start */
.section_of_products {
  max-width: 1440px;
  margin: 0 auto;
  text-align: center;
  margin-top: 80px;
}
