/* Sections at home page END */

/* Footer start */
.footer_container {
  background-color: #06163a;
  border-top-width: 1px;
  border-right-width: 0px;
  border-bottom-width: 1px;
  border-left-width: 0px;
  display: flex;
  align-items: center;
}
