/* Products by Category Start */
.header_text {
  max-width: 1328px;
  margin: 0 auto;
  padding: 0 5%;
  padding-top: 5%;
}
.header_text p {
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: #0a0607;
}
.products_grid_container {
  max-width: 1328px;
  margin: 0 auto;
  padding: 5% 3%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(171px, 1fr));
  column-gap: 2px;
  row-gap: 30px;
}

.products_grid_card {
  max-width: 171px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 10%;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.products_grid_card:hover {
  box-shadow: 0px 0px 10px 5px rgba(122, 122, 122, 0.6);
}

.products_grid_card_img {
  max-width: 171px;
  overflow: hidden; /* Чтобы изображение не выходило за границы */
}

.products_grid_card_img img {
  width: 100%;
  transition: transform 0.3s ease-in-out;
}

.products_grid_card:hover .products_grid_card_img img {
  transform: scale(1.1);
}

.products_grid_card h3 {
  font-size: 18px;
  line-height: 22px;
  color: #0a0607;
  font-weight: 500;
  width: 100%;
}
