/* Single page END */

/* Blog Page start */

.blogs_grid_container {
  max-width: 1328px;
  margin: 0 auto;
  padding: 0 3%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(362px, 1fr));
  padding-top: 5%;
  padding-bottom: 5%;
  gap: 15px;
}
.blog_category_name {
  font-size: 16px;
  line-height: 24px;
  color: #0a0607;
}
.blog_title {
  font-size: 22px;
  line-height: 26px;
  color: #0a0607;
  font-weight: 400;
}
.time_blog {
  font-size: 12px;
  line-height: 14px;
  color: #3f485d;
}
.blog_short_desc {
  font-size: 16px;
  line-height: 24px;
  color: #0a0607;
}
.blogs_grid_card {
  max-width: 362px;
  padding: 10px;
  transition: all 0.4s ease-in-out;
}
.blogs_grid_card_img {
  overflow: hidden;
}
.blogs_grid_card_img img {
  width: 100%;
  transition: transform 0.4s ease-in-out;
}

.blogs_card_content {
  max-width: 342px;
  padding: 8%;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.blogs_grid_card:hover {
  box-shadow: 0px 0px 4px 8px rgba(166, 166, 166, 0.55);
}
.blogs_grid_card:hover .blogs_grid_card_img img {
  transform: scale(1.1);
}
.blog_btn {
  width: 70px;
  border: none;
  text-align: left;
  background: none;
  cursor: pointer;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  color: #d42027;
  border-bottom: 1px solid #d32027;
  padding: 5px 0px;
}
/* Blog Page END */
/* Contact START */

.contact_section {
  border-top: 1px solid rgba(38, 38, 38, 0.1);
}
.contact_section h2 {
  font-size: 48px;
  font-weight: 300;
  line-height: 54px;
  color: #000;
  text-align: center;
  padding: 5% 0;
}
.contact_content,
.information_content {
  display: flex;
}
.information_content {
  gap: 10px;
}
.contact_content {
  max-width: 1328px;
  margin: 0 auto;
  padding: 0 3%;
  display: flex;
  justify-content: space-between;
}
.information_content_ {
  max-width: 373px;
  display: flex;
  flex-direction: column;
}
.information_content_ h3,
span {
  font-size: 22px;
  line-height: 26px;
  font-weight: 400;
  color: #020d26;
}
.information_content_ p {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #020d26;
  margin-bottom: 30px;
}
.information_content img {
  width: 60px;
  height: 60px;
}

.form_content {
  max-width: 748px;
  display: flex;
  justify-content: center;
  width: 100%;
}
form {
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.form-row {
  display: flex;
  gap: 10px;
}

.form-row div {
  flex: 1;
}

label {
  font-weight: bold;
  font-size: 14px;
}
input {
  margin-top: 10px;
}

input,
textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  transition: border 0.3s ease;
}

input:hover,
textarea:hover,
input:focus,
textarea:focus {
  border-color: #d42027;
  outline: none;
}

textarea {
  resize: none;
  height: 80px;
}

button {
  background: #d42027;
  color: white;
  border: none;
  padding: 12px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s ease;
}

button:hover {
  background: #a5171f;
}
.google_map {
  max-width: 1328px;
  margin: 0 auto;
  height: 300px;
  padding: 0 3%;
  margin-top: 50px;
  margin-bottom: 60px;
}
.gmap_iframe {
  width: 100%;
  height: 100%;
}
/* Contact END */
